pact of the permanent closure of three uncoated freesheet machines in 2005 . operating earnings in 2006 more than doubled compared with both 2005 and 2004 . the benefits of improved aver- age sales price realizations more than offset higher input costs for freight , wood and energy , which were all above 2005 levels . mill operations were favorable compared with 2005 due to current-year improve- ments in machine performance , lower labor , chem- ical and energy consumption costs , as well as approximately $ 30 million of charges incurred in 2005 for machine shutdowns . coated papers net sales were $ 920 million in 2006 , $ 1.6 billion in 2005 and $ 1.4 billion in 2004 . operating profits in 2006 were 26% ( 26 % ) lower than in 2005 . a small operating loss was reported for the business in 2004 . this business was sold in the third quarter of 2006 . during the first two quarters of 2006 , sales volumes were up slightly versus 2005 . average sales price realizations for coated freesheet paper and coated groundwood paper were higher than in 2005 , reflecting the impact of previously announced price increases . however , input costs for energy , wood and other raw materials increased over 2005 levels . manufacturing operations were favorable due to higher machine efficiency and mill cost savings . market pulp sales in 2006 were $ 509 mil- lion , compared with $ 526 million and $ 437 million in 2005 and 2004 , respectively . sales volumes in 2006 were down from 2005 levels , primarily for paper and tissue pulp . average sales price realizations were higher in 2006 , reflecting higher average prices for fluff pulp and bleached hardwood and softwood pulp . operating earnings increased 30% ( 30 % ) from 2005 and more than 100% ( 100 % ) from 2004 principally due to the impact of the higher average sales prices . input costs for wood and energy were higher in 2006 than in 2005 . manufacturing operations were unfavorable , driven primarily by poor operations at our riegel- wood , north carolina mill . brazil ian paper net sales for 2006 of $ 496 mil- lion were higher than the $ 465 million in 2005 and the $ 417 million in 2004 . the sales increase in 2006 reflects higher sales volumes than in 2005 , partic- ularly for uncoated freesheet paper , and a strengthening of the brazilian currency versus the u.s . average sales price realizations improved in 2006 , primarily for uncoated freesheet paper and wood chips . despite higher net sales , operating profits for 2006 of $ 122 million were down from $ 134 million in 2005 and $ 166 million in 2004 , due principally to incremental costs associated with an extended mill outage in mogi guacu to convert to an elemental-chlorine-free bleaching process , to rebuild the primary recovery boiler , and for other environmental upgrades . european papers net sales in 2006 were $ 1.5 bil- lion , compared with $ 1.4 billion in 2005 and $ 1.5 bil- lion in 2004 . sales volumes in 2006 were higher than in 2005 at our eastern european mills due to stron- ger market demand . average sales price realizations increased in 2006 in both eastern and western european markets . operating earnings in 2006 rose 20% ( 20 % ) from 2005 , but were 15% ( 15 % ) below 2004 levels . the improvement in 2006 compared with 2005 .",
