    loadAnswer() {
  setTimeout(() => {
    const selectedQuestion = this.selectedQuestion;
    const selectedOption = this.jsonData.find(item => item.question === selectedQuestion);

    if (selectedOption) {
      const programText = selectedOption.program;
      const answerText = selectedOption.answer.toString(); // 将浮点数转换为字符串形式

      // 定时器处理 program 的流式输出
      this.currentCharIndex1 = 0;
      this.intervalId1 = setInterval(() => {
        if (this.currentCharIndex1 <= programText.length) {
          this.displayText1 = programText.slice(0, this.currentCharIndex1);
          this.currentCharIndex1++;
        } else {
          clearInterval(this.intervalId1);
        }
      }, 100); // 每100毫秒显示一个字符

      // 定时器处理 answer 的流式输出
      setTimeout(() => { this.currentCharIndex2 = 0;
